* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #f8fafc;
  min-height: 100vh;
  color: #334155;
  position: relative;
}

body::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: 
    radial-gradient(circle at 20% 80%, rgba(59, 130, 246, 0.08) 0%, transparent 50%),
    radial-gradient(circle at 80% 20%, rgba(124, 58, 237, 0.06) 0%, transparent 50%),
    radial-gradient(circle at 40% 40%, rgba(30, 64, 175, 0.04) 0%, transparent 50%);
  pointer-events: none;
  z-index: -1;
}

/* Top background rectangle */
body::after {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 400px;
  background: url('./mathematical-background.jpg') center center;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  pointer-events: none;
  z-index: -1;
}

/* Left sidebar background rectangle */
.sidebar-bg {
  position: fixed;
  top: 0;
  left: 0;
  width: 118px;
  height: 100vh;
  background: rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(16px);
  border-right: 1px solid rgba(203, 213, 225, 0.3);
  z-index: 999;
  pointer-events: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Modal Styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2000;
  backdrop-filter: blur(5px);
}

.modal-content {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(203, 213, 225, 0.3);
  border-radius: 8px;
  padding: 40px;
  max-width: 500px;
  width: 90%;
  max-height: 90vh;
  overflow-y: auto;
  position: relative;
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.15);
  animation: modalSlideIn 0.3s ease-out;
}

@keyframes modalSlideIn {
  from {
    opacity: 0;
    transform: translateY(-50px) scale(0.9);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

.modal-close {
  position: absolute;
  top: 15px;
  right: 20px;
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
  color: #64748b;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
  transition: all 0.3s ease;
}

.modal-close:hover {
  background: rgba(100, 116, 139, 0.1);
  color: #334155;
}

.modal-content h3 {
  color: #1e293b;
  font-size: 1.8rem;
  margin-bottom: 25px;
  text-align: center;
}

/* Form Styles */
.form-group {
  margin-bottom: 20px;
}

.form-group label {
  display: block;
  margin-bottom: 8px;
  font-weight: 600;
  color: #334155;
  font-size: 14px;
}

.form-group input,
.form-group textarea {
  width: 100%;
  padding: 12px 15px;
  border: 2px solid #cbd5e1;
  border-radius: 6px;
  font-size: 16px;
  transition: all 0.3s ease;
  font-family: inherit;
  background: rgba(255, 255, 255, 0.8);
  color: #334155;
}

.form-group input:focus,
.form-group textarea:focus {
  outline: none;
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
  background: rgba(255, 255, 255, 1);
}

.form-group textarea {
  resize: vertical;
  min-height: 120px;
}

.form-buttons {
  display: flex;
  gap: 15px;
  justify-content: center;
  margin-top: 30px;
}

.form-buttons .btn {
  min-width: 120px;
}

/* Shortcut Buttons - Top Left Corner */
.shortcut-buttons {
  position: fixed;
  top: 40px;
  left: 18px;
  z-index: 1001;
  display: flex;
  flex-direction: column;
  gap: 7px;
  width: 82px;
}

.shortcut-btn {
  background: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(16px);
  border: 1px solid rgba(203, 213, 225, 0.3);
  padding: 6px 9px;
  border-radius: 4px;
  font-size: 14px;
  font-weight: 500;
  color: #334155;
  cursor: pointer;
  transition: all 0.2s ease;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.05);
  letter-spacing: 0.025em;
}

.shortcut-btn:hover {
  background: rgba(255, 255, 255, 0.95);
  transform: translateY(-1px);
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
  border-color: rgba(59, 130, 246, 0.3);
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  padding-left: 141px; /* Add space for sidebar */
  padding-top: 100px; /* Add space for shortcut buttons */
}

.card {
  background: rgba(255, 255, 255, 0.7);
  backdrop-filter: blur(16px);
  border: 1px solid rgba(203, 213, 225, 0.3);
  border-radius: 8px;
  padding: 40px;
  margin: 20px 0;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.05);
  transition: all 0.2s ease;
}

.card:hover {
  transform: translateY(-2px);
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1);
  border-color: rgba(59, 130, 246, 0.3);
}

.btn {
  display: inline-block;
  padding: 14px 28px;
  background: rgba(59, 130, 246, 0.9);
  color: white;
  text-decoration: none;
  border-radius: 6px;
  font-weight: 500;
  transition: all 0.2s ease;
  border: 1px solid rgba(59, 130, 246, 0.3);
  cursor: pointer;
  font-size: 15px;
  margin: 5px;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
  letter-spacing: 0.025em;
  backdrop-filter: blur(16px);
}

.btn:hover {
  transform: translateY(-1px);
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
  background: rgba(59, 130, 246, 1);
  border-color: rgba(59, 130, 246, 0.5);
}

.btn-secondary {
  background: rgba(124, 58, 237, 0.9);
  border-color: rgba(124, 58, 237, 0.3);
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
}

.btn-secondary:hover {
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
  background: rgba(124, 58, 237, 1);
  border-color: rgba(124, 58, 237, 0.5);
}

.section {
  margin: 40px 0;
}

.section h2 {
  color: #1e293b;
  font-size: 2.75rem;
  font-weight: 300;
  margin-bottom: 30px;
  text-align: center;
  letter-spacing: -0.025em;
  line-height: 1.2;
  text-shadow: 0 2px 4px rgba(255, 255, 255, 0.8);
}

.section h3 {
  color: #1e293b;
  font-size: 1.75rem;
  font-weight: 400;
  margin-bottom: 20px;
  letter-spacing: -0.025em;
}

.section h4 {
  color: #334155;
  font-size: 1.25rem;
  font-weight: 500;
  margin: 25px 0 12px 0;
  letter-spacing: -0.025em;
}

.link-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
  margin-top: 20px;
}

.link-item {
  background: rgba(255, 255, 255, 0.7);
  backdrop-filter: blur(16px);
  border: 1px solid rgba(203, 213, 225, 0.3);
  border-radius: 8px;
  padding: 28px;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.05);
  transition: all 0.2s ease;
  border-left: 3px solid rgba(59, 130, 246, 0.6);
}

.link-item:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
  border-color: rgba(59, 130, 246, 0.4);
  background: rgba(255, 255, 255, 0.9);
}

.link-item h4 {
  color: #1e293b;
  margin-bottom: 10px;
  font-size: 1.2rem;
}

.link-item p {
  color: #475569;
  margin-bottom: 15px;
  line-height: 1.6;
}

.link-item a {
  color: #3b82f6;
  text-decoration: none;
  font-weight: 600;
  transition: color 0.3s ease;
}

.link-item a:hover {
  color: #1d4ed8;
}

/* Profile Section with Picture */
.profile-section {
  margin-bottom: 40px;
}

.profile-content {
  display: flex;
  align-items: center;
  gap: 40px;
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(203, 213, 225, 0.4);
  border-radius: 8px;
  padding: 48px;
  box-shadow: 0 20px 40px -10px rgba(0, 0, 0, 0.2);
  margin-top: 20px;
  margin-bottom: 40px;
}

.profile-pic-container {
  flex-shrink: 0;
}

.profile-pic {
  width: 240px;
  height: 240px;
  border-radius: 50%;
  background: rgba(59, 130, 246, 0.1);
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.05);
  overflow: hidden;
  border: 2px solid rgba(59, 130, 246, 0.3);
  backdrop-filter: blur(16px);
}

.profile-pic img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
}

.profile-pic-placeholder {
  font-size: 60px;
  color: white;
}

.profile-info {
  flex: 1;
}

.profile-info h1 {
  color: #1e293b;
  font-size: 2.75rem;
  font-weight: 300;
  margin-bottom: 20px;
  letter-spacing: -0.025em;
  line-height: 1.2;
}

.profile-info p {
  color: #475569;
  font-size: 1.125rem;
  margin-bottom: 30px;
  line-height: 1.7;
  font-weight: 400;
  text-align: justify;
}

.social-buttons {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  align-items: center;
}

/* Social Icons */
.social-icons {
  display: flex;
  gap: 15px;
  margin-left: 15px;
}

.social-icon {
  display: inline-block;
  font-size: 28px;
  text-decoration: none;
  transition: all 0.3s ease;
  padding: 8px;
  border-radius: 6px;
  background: rgba(59, 130, 246, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(59, 130, 246, 0.2);
}

.social-icon:hover {
  transform: translateY(-3px) scale(1.1);
  background: rgba(59, 130, 246, 0.2);
  box-shadow: 0 5px 15px rgba(59, 130, 246, 0.2);
  border-color: rgba(59, 130, 246, 0.3);
}

.social-icon img {
  width: 28px;
  height: 28px;
  object-fit: contain;
  border-radius: 3px;
}

/* About section lists */
.card ul {
  margin-left: 20px;
  margin-bottom: 20px;
}

.card li {
  margin-bottom: 8px;
  line-height: 1.6;
  color: #475569;
}

@media (max-width: 768px) {
  .container {
    padding: 15px;
    padding-left: 15px;
    padding-top: 120px;
  }
  
  .sidebar-bg {
    display: none;
  }
  
  .shortcut-buttons {
    top: 10px;
    left: 10px;
    flex-direction: row;
    flex-wrap: wrap;
    max-width: calc(100vw - 20px);
    width: auto;
  }
  
  .shortcut-btn {
    font-size: 11px;
    padding: 6px 10px;
  }
  
  .section h2 {
    font-size: 2rem;
  }
  
  .profile-info h1 {
    font-size: 2rem;
  }
  
  .link-grid {
    grid-template-columns: 1fr;
  }
  
  .profile-content {
    flex-direction: column;
    text-align: center;
    gap: 20px;
    padding: 30px 20px;
  }
  
  .profile-pic {
    width: 120px;
    height: 120px;
  }
  
  .profile-pic-placeholder {
    font-size: 50px;
  }
  
  .social-buttons {
    justify-content: center;
    flex-direction: column;
    gap: 15px;
  }
  
  .social-icons {
    margin-left: 0;
    justify-content: center;
  }
  
  .social-icon {
    font-size: 24px;
    padding: 6px;
  }
  
  .btn {
    font-size: 14px;
    padding: 10px 20px;
  }
  
  .modal-content {
    width: 95%;
    padding: 20px;
    margin: 20px;
  }
  
  .form-buttons {
    flex-direction: column;
    align-items: center;
  }
  
  .form-buttons .btn {
    width: 100%;
    max-width: 200px;
  }
} 